.inputBox {
  width: 100%;
  height: 40px;
  margin-top: 10px;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background-image: linear-gradient(to right, rgba(0, 230, 255, 1), rgba(255, 43, 146, 1));

    border-radius: 5px;
  }

  .inputDom {
    width: 100%;
    height: 100%;
    background: var(--g-theme-custom-input-bg);
    color: var(--g-theme-color);
    position: relative;
    z-index: 1;
    border: none;
    outline: none;
    border-radius: 4px;
    padding-left: 15px;
  }
}


div::-webkit-scrollbar {
  width: 4px;
}

div::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  opacity: 0.2;
  background: fade(@primary-color, 60%);
}

div::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 0;
}

// 
.global_message {
  .ant-message-notice-content {
    background: var(--g-theme-bg);
    color: var(--g-theme-color);
    border-radius: 5px;
  }
}

input {

  // 
  &:autofill {
    background: #fff;
  }

  // chrome
  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus,
  &:-webkit-autofill:active {
    transition: background-color 5000s !important;
    -webkit-text-fill-color: rgba(0, 0, 0, 0.65) !important;
  }
}

.App {
  width: 100%;
  height: 100%;



  .LayoutContent {
    width: 100%;
    height: 100%;

    .layout-content_warp {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;

      @media only screen and (max-width: 600px) {
        justify-content: center;
        flex-direction: column;
      }

      .layout-content-router {
        width: 100%;
        height: 100%;
        overflow: hidden;

        @media only screen and (max-width: 600px) {
          height: calc(100% - 60px);
        }
      }

      .mobile_bottom-tabBar {
        width: 100%;
        height: 55px;
        background: var(--g-theme-menuBg);
        color: var(--g-theme-color);
        display: none;

       
        @media only screen and (max-width: 600px) {
          display: grid;
          grid-template-columns: repeat(5, minmax(0px, 1fr));
          gap: 10px;

          .tabBar_item-img {
            width: 25px;
            height: 25px;
            display: block;
            margin: 0 auto;
            margin-top: 5px;
          }

          .menuLightDisplay {
            display: var(--g-theme-menu-light-display);
            margin: 0 auto;
            margin-top: 5px;
          }

          .menuDarkDisplay {
            display: var(--g-theme-menu-dark-display);
            margin: 0 auto;
            margin-top: 5px;
          }


          .tabBar_item-name {
            display: block;
            width: 100%;
            text-align: center;
            text-overflow: ellipsis;
            width: 100%;
            white-space: nowrap;
            overflow: hidden;
          }
        }

      }
    }
  }

  .ant-layout {
    width: 100%;
    height: 100% !important;
    background: var(--g-theme-bg) !important;

    .ant-layout-content {
      width: 100%;
      height: 100%;
    }
  }

  .warp {
    width: 50%;
    margin: 0 auto;
    overflow: auto;
  }

  #map {
    width: 100%;
    height: 500px;
  }
}
